.zolo-flip-box_wrap {
    --zolo-inner-item-height: 500px;
    --zolo-content-padding: 50px;
    --zolo-icon-margin: 0 0 8px;
    --zolo-icon-border-radius: 4px;
    --zolo-icon-color: #fff;
    --zolo-icon-img-size: 50px;
    --zolo-title-text-color: #fff;
    --zolo-title-margin: 0 0 14px;
    --zolo-desc-margin: 0;
    --zolo-desc-text-color: #e1e1e1;
    --zolo-link-btn-gap: 10px;
    --zolo-link-btn-text-color: #fff;
    --zolo-link-btn-text-hover-color: #fff;
    --zolo-link-bnt-padding: 14px 30px;
    --zolo-link-btn-icon-size: 1em;
    --zolo-desc-back-margin: 0 0 24px;
    --zolo-desc-back-text-color: #545455;
    --zolo-back-title-text-color: #1f2024;
    --zolo-back-icon-color: #1f2024;

    --zolo-back-item-border-width: 1px;
    --zolo-back-item-border-style: solid;

    .zolo-flip-box_item {
        position: relative;
        perspective: 1200px;
    }

    .zolo-flip-box_inner-item {
        position: relative;
        transition-property: box-shadow, transform;
        transition-duration: 0.6s;
        transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
        width: 100%;
        height: var(--zolo-inner-item-height);
    }

    .zolo-flip-box_inner-item,
    .zolo-flip-box_face {
        transform-style: preserve-3d;
    }

    .zolo-flip-box_face {
        backface-visibility: hidden;
        width: 100%;
        height: 100%;
        box-sizing: border-box;
    }

    .zolo-flip-box_back {
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
    }

    .zolo-flip-box_front {
        background-color: var(--zoloblocks-brand-color);
        .zolo-flip-box-content {
            .zolo-flip-box_desc {
                margin: 0;
            }
        }
    }

    .zolo-flip-box_img {
        position: relative;
    }

    // .zolo-flip-box_face:before {
    //     position: absolute;
    //     content: "";
    //     top: 0;
    //     left: 0;
    //     width: 100%;
    //     height: 100%;
    //     background: linear-gradient(57deg, rgba(9, 11, 20, 0.484), transparent);
    // }

    .zolo-flip-box-content {
        padding: var(--zolo-content-padding);
        height: 100%;
        width: 100%;
        display: inline-flex;
        flex-direction: column;
        box-sizing: border-box;
        backface-visibility: hidden;
        z-index: 1;
        position: relative;
    }

    .zolo-flip-box_front-content {
        text-align: center;
        justify-content: center;
    }

    .zolo-flip-box_back-content {
        text-align: center;
        justify-content: center;
    }

    .zolo-flip-box_icon {
        margin: var(--zolo-icon-margin);
    }

    .zolo-flip-box_inner-icon {
        display: inline-block;
        overflow: hidden;
        border-radius: var(--zolo-icon-border-radius);
        &.zolo-icon {
            font-size: 40px;
            svg path {
                fill: var(--zolo-icon-color);
            }
        }
        &.zolo-image {
            overflow: hidden;
            img {
                width: var(--zolo-icon-img-size);
                height: var(--zolo-icon-img-size);
                vertical-align: middle;
            }
        }
    }

    .title-link {
        text-decoration: none;
    }

    .zolo-flip-box_title {
        margin: var(--zolo-title-margin);
        font-size: 30px;
        font-weight: 700;
        color: var(--zolo-title-text-color);
    }

    .zolo-flip-box_desc {
        margin: var(--zolo-desc-margin);
        color: var(--zolo-desc-text-color);
        font-size: 16px;
        text-wrap: balance;
        font-weight: 400;
    }

    .zolo-flip-box_link-btn {
        display: inline-flex;
        align-items: center;
        gap: var(--zolo-link-btn-gap);
        color: var(--zolo-link-btn-text-color);
        border-radius: var(--zoloblocks-button-border-radius);
        background-color: var(--zoloblocks-brand-color);
        padding: var(--zolo-link-bnt-padding);
        font-size: 16px;
        text-decoration: none;
        transition: all 0.3s ease 0s;
        &:hover {
            color: var(--zolo-link-btn-text-hover-color);
            background-color: var(--zoloblocks-brand-color-hover);
        }
        .zolo__display-icon svg {
            width: var(--zolo-link-btn-icon-size);
            height: var(--zolo-link-btn-icon-size);
            display: block;
            path {
                fill: currentColor;
            }
        }
    }

    .zolo-flip-box_link-btn svg,
    .zolo-flip-box_icon svg {
        width: 1em;
        height: 1em;
        display: block;
    }

    .zolo-flip-box_front .zolo-flip-box-content {
        transform: translate3d(0, 0, 60px) scale(1.048);
    }

    .zolo-flip-box_back {
        background-color: var(--zoloblocks-item-bg-color);
        border-width: var(--zolo-back-item-border-width);
        border-style: var(--zolo-back-item-border-style);
        border-color: var(--zoloblocks-item-border-color);
        .zolo-flip-box-content {
            transform: translate3d(0, 0, 60px) scale(1.048);
        }
        .zolo-flip-box_desc {
            margin: var(--zolo-desc-back-margin);
            color: var(--zolo-desc-back-text-color);
        }
        .zolo-flip-box_title {
            color: var(--zolo-back-title-text-color);
        }
        .zolo-flip-box_inner-icon {
            svg path {
                fill: var(--zolo-back-icon-color);
            }
        }
    }
}

/*  Animation Style 1 */

.zolo-flip-box_animation_style-1 .zolo-flip-box_back {
    transform: rotateY(180deg) translateZ(1px);
}

.zolo-flip-box_animation_style-1 .zolo-flip-box_hover.zolo-flip-box_item:hover .zolo-flip-box_inner-item,
.zolo-flip-box_animation_style-1 .zolo-flip-box_active.zolo-flip-box_item .zolo-flip-box_inner-item {
    transform: rotateY(180deg);
    transform-origin: 50% 50%;
}

/*  Animation Style 2 */
.zolo-flip-box_animation_style-2 .zolo-flip-box_back {
    transform: rotateY(-180deg) rotateZ(-180deg);
}

.zolo-flip-box_animation_style-2 .zolo-flip-box_hover.zolo-flip-box_item:hover .zolo-flip-box_inner-item,
.zolo-flip-box_animation_style-2 .zolo-flip-box_active.zolo-flip-box_item .zolo-flip-box_inner-item {
    transform: rotateX(180deg);
    transform-origin: 50% 50%;
}

/*  Animation Style 3 */

.zolo-flip-box_animation_style-3 .zolo-flip-box_back {
    transform: rotateY(180deg) translateZ(1px);
}

.zolo-flip-box_animation_style-3 .zolo-flip-box_hover.zolo-flip-box_item:hover .zolo-flip-box_inner-item,
.zolo-flip-box_animation_style-3 .zolo-flip-box_active.zolo-flip-box_item .zolo-flip-box_inner-item {
    transform: rotateY(-180deg);
    transform-origin: 50% 50%;
}

/*  Animation Style 4 */
.zolo-flip-box_animation_style-4 .zolo-flip-box_back {
    transform: rotateY(-180deg) rotateZ(-180deg);
}

.zolo-flip-box_animation_style-4 .zolo-flip-box_hover.zolo-flip-box_item:hover .zolo-flip-box_inner-item,
.zolo-flip-box_animation_style-4 .zolo-flip-box_active.zolo-flip-box_item .zolo-flip-box_inner-item {
    transform: rotateX(-180deg);
    transform-origin: 50% 50%;
}

